<template>
    <div class="loadForecast">
      <h3 class="margin-bottom">负荷预测</h3>
      <el-row type="flex" class="row-bg" justify="space-between">
        <el-col :span="14">
            <TreeOrg :treeData="treeData" v-on:PVviewById="PVviewById"></TreeOrg>
            <div class="left chart-wrap order-container">
              <p>综合能源需求预测</p>
              <div id="loadForecast"></div>
            </div>
        </el-col>
        <el-col :span="10">1</el-col>
      </el-row>
      <!--<img src="../../../assets/img/ui/03-01多能优化-多能负荷预测.jpg" alt="">-->
    </div>
</template>

<script>
  import echarts from "echarts";
  import TreeOrg from "../../common/TreeOrg.vue";
    export default {
        data () {
            return {
              treeData:[
                {
                  id:11,
                  label: "斗门区 (12)",
                  children: [
                    {
                      id:21,
                      label: "珠海新青科技工业园太阳能光伏发电项目(伟创力厂区)"
                    },
                    {
                      id:22,
                      label: "珠海新青科技工业园太阳能光伏发电项目(伟创力厂区)"
                    },
                    {
                      id:23,
                      label: "珠海新青科技工业园太阳能光伏发电项目(伟创力厂区)"
                    }
                  ]
                },
                {
                  id:12,
                  label: "金湾区 (6)",
                  children: [
                    {
                      label: "二级 2-1",
                      children: [
                        {
                          label: "三级 2-1-1"
                        }
                      ]
                    },
                    {
                      label: "二级 2-2",
                      children: [
                        {
                          label: "三级 2-2-1"
                        }
                      ]
                    }
                  ]
                },
                {
                  label: "横琴新区 (6)",
                  children: [
                    {
                      label: "二级 3-1",
                      children: [
                        {
                          label: "三级 3-1-1"
                        }
                      ]
                    },
                    {
                      label: "二级 3-2",
                      children: [
                        {
                          label: "三级 3-2-1"
                        }
                      ]
                    }
                  ]
                }
              ],
            }
        },
      mounted(){
        let that = this;
        that.getdayEnergyUse();
      },
      methods:{
        PVviewById:function(id){

        },
        getdayEnergyUse() {
          var that = this;
          var xData = [];
          var powerData = [];
          var gasData = [];
          var heatData = [];
          for(var i=0;i<24;i++){
            xData.push(i);
            powerData.push(Math.round(Math.random()*2*10));
            gasData.push(Math.round(Math.random()*2*10));
            heatData.push(Math.round(Math.random()*2*10));
          }
          // 基于准备好的dom，初始化echarts实例
          var myChart = echarts.init(document.getElementById("loadForecast"));
          // 指定图表的配置项和数据
          var option = {
//        dataset: {
//          source: that.data
//        },
            color: ["#0A6EDA", "#f5212f","#F5A623"],
            title: {
              text: ""
            },
            tooltip: {
              trigger: "axis",
              axisPointer: {
                type: "cross",
                crossStyle: {
                  color: "#999"
                }
              }
            },
//            legend: {
//              data: ["电力负荷", "热力负荷","燃气负荷"],
//              right: 0
//            },
            grid: {
              x: "8%",
              y: "15%",
              x2: "3%",
              y2: "20%"
            },
            xAxis: {
              type: "category",
              //X轴数字
              axisLabel: {
                color: "#2B2B2B"
              },
              //X轴刻度线
              axisTick: {
                show: false
              },
              //纵向坐标网格线
              splitLine: {
                show: false,
                lineStyle: {
                  color: "#e6e8ed"
                }
              },
              //X轴坐标线
              axisLine: {
                lineStyle: {
                  color: "#e6e8ed"
                }
              },
              boundaryGap: false,
              data: xData
            },
            yAxis: {
              type: "value",
              name:'%',
              min:0,
              max:100,
              //Y轴数字
              axisLabel: {
                color: "#2B2B2B"
              },
              //Y轴刻度线
              axisTick: {
                show: false
              },
              //横向坐标网格线
              splitLine: {
                lineStyle: {
                  color: "#e6e8ed"
                }
              },
              //Y轴坐标线
              axisLine: {
                lineStyle: {
                  color: "#e6e8ed"
                }
              }
            },
            series: [
              {
                name: "电力负荷",
                type: "line",
                symbol: 'none',
                stack: "总量",
                data:powerData,
              },
              {
                name: "热力负荷",
                type: "line",
                symbol: 'none',
                stack: "总量",
                data:gasData,
              },
              {
                name: "燃气负荷",
                type: "line",
                symbol: 'none',
                stack: "总量",
                data:heatData,
              }
            ]
          };
          // 使用刚指定的配置项和数据显示图表。
          myChart.setOption(option);
        },
      },
      components: {
        TreeOrg
      },
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#loadForecast{
  height: 332px;
}
</style>



